.products {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;

    @media (min-width: 320px) and (max-width: 767px) {
        padding-top: 14px;
    }

    @media (min-width: 768px) and (max-width: 1199px) {
        padding: 40px 15px 0 15px;
    }

    @media (min-width: 320px) and (max-width: 1199px) {
        position: relative;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(1, 1fr);
        gap: 30px;

        @media (min-width: 320px) and (max-width: 767px) {
            grid-template-columns: repeat(1, 1fr);
            grid-template-rows: repeat(4, 1fr);
        }

        @media (min-width: 768px) and (max-width: 1199px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(2, 1fr);
        }

        @media (min-width: 1200px) {
            gap: 15px;
        }
    }
}

.product-card {
    background: #FFFFFF;
    border-radius: 20px;
    width: 255px;
    height: 355px;
    position: relative;
    display: inline-block;

    &:hover {
        box-shadow: 0px 0px 23px rgba(0, 0, 0, 0.15);
    }

    &__main-img {
        height: 160px;
        margin: 15px auto 0 auto;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__heart {
        position: absolute;
        right: 20px;
        top: 20px;
        opacity: 0;
    }

    &__btn-delete {
        border: none;
        background: none;
        cursor: pointer;
        position: absolute;
        right: 20px;
        top: 20px;
        display: none;
        opacity: 0;

        img {
            width: 16px;
            height: 16px;
        }

    }

    &__info-product {
        background: rgba(140, 50, 19, 0.06);
        border-radius: 20px;
        margin: 20px 10px 10px 10px;
        display: flex;
        position: relative;
        padding: 21px 0 29px 20px;
    }

    &__name {
        padding-bottom: 40px;
    }

    &__link {
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
        text-decoration: none;
        position: relative;

        &:before {
            content: '';
            position: absolute;
            left: 0;
            bottom: -5px;
            width: 100%;
            height: 3px;
            background-color: #84BE51;
            transform: scaleX(0);
            transform-origin: rigth;
            transition: transform 0.25s;
        }
    }

    &__price {
        padding-bottom: 3px;
        font-weight: 700;
        font-size: 18px;
        line-height: 22px;
        color: #84BE51;
    }

    &__quantity {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
    }

    &__cart {
        position: absolute;
        right: 20px;
        bottom: 25px;
        cursor: pointer;
    }


}

.product-card:hover {
    .product-card__heart {
        opacity: 1;
    }

    .product-card__link:before {
        transform: scaleX(1);
        transition: transform 0.35s;
        transform-origin: left;
    }

    .product-card__btn-delete {
        opacity: 1;
    }
}

.product-card .product-card__heart .circle-icon_click {
    background: #8C3213;

    svg {
        fill: #FFFFFF;
    }
}

.product-card .product-card__heart_click {
    opacity: 1;
}

.carousel {
    margin: 0 auto;
    overflow: hidden;

    @media (min-width: 1200px) {
        max-width: 1120px;
    }

    &__items {
        white-space: nowrap;
        transition: ease 1s;

        &_big {
            @media (min-width: 320px) and (max-width: 767px) {
                display: none;
            }
        }

        &_small {
            @media (min-width: 768px) {
                display: none;
            }
        }
    }
}

.carousel .product-card {
    margin-left: 20px;
}